import {ApplyIstitle,ApplyDistributor} from 'api/user'
export default {
	data() {
		return {
			baseUrl: this.$store.state.SRC + '/config/app',
			phone: "",
			realname: '',
			title: "",
			progressStatus: 0,
			wechatpay: '',
			alipay: '',
			bank: '',
			introduction: '',
			cardId: '',
			logoUrl: '',
			qrCode: '',
			state: {},
			isAuth: false,
			url:''
		}
	},
	methods: {
		open(){
			uni.switchTab({
				url:'/pages/user/index'
			})
		},
		submit(){
			ApplyDistributor({
				title:this.title,
				logo:this.logoUrl,
				realname:this.realname,
				mobile:this.phone,
				intro:this.introduction,
				bankname:this.bank,
				bankcard:this.cardId,
				alipayno:this.alipay,
				weipayno:this.wechatpay
			}).then(res=>{
				if(res.result){
					this.progressStatus = 2
					this.qrCode = this.$store.state.SRC+res.QRcode
					this.url = res.url
				}else{
					uni.showToast({
						title:res.msg,
						icon:'none'
					})
				}
			})
			
		},
		checkName(){
			ApplyIstitle({
				title:this.title
			}).then(res=>{
				if(res.result){
					this.progressStatus=1
				}else{
					uni.showToast({
						title:res.msg,
						icon:'none'
					})
				}
			})
			
		},
		uploadImg() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: res => {
					uni.uploadFile({
						url:this.state.SRC+'/webapi/School/UploadFile',
						filePath:res.tempFilePaths[0],
						fileType:'image',
						name:'file',
						formData:{
							apptoken:this.state.appToken,
							userid:this.state.userId
						},
						success:resdata=>{
							let data = JSON.parse(resdata.data)
							if(data.result){
								this.logoUrl = this.state.SRC+data.fileUrl
							}else{
								this.$msg(data.msg)
							}
						}
					})
				}
			});
		},

		save() {
			var that = this
			if (this.qrCode) {
				uni.getSetting({
					success(res) {

						if (res.authSetting['scope.writePhotosAlbum']) {
							that.isAuth = true

							uni.getImageInfo({
								src: that.qrCode,
								success: function(image) {

									uni.saveImageToPhotosAlbum({
										filePath: image.path,
										success: function() {

											uni.showToast({
												title: '保存成功'
											})
										}

									});
								}
							});

						} else {
							that.isAuth = false

						}
					}
				})
			}
		}
























	},

	onShow() {
		var that = this
		uni.getSetting({
			success(res) {
				that.isAuth = res.authSetting['scope.writePhotosAlbum'] ? true : false
				if (!that.isAuth) {
					uni.authorize({
						scope: 'scope.writePhotosAlbum',
						success() {

						}
					})
				}
			}
		})
	},

	onLoad() {
		this.$store.ready(state => {
			this.state = state
		})
	}
}
